import React, { useContext, useEffect, useState } from 'react';
import {auth} from '../database/firebase'; 

/* Creating A Context for User Login */
const UserLogin = React.createContext();

/* Creating A function for accessing the context Consumer */
export function useAuth(){
    return useContext(UserLogin);
}

export function AuthContext({children}){

    const [currentUser,setCurrentUser] = useState('');
    const [loading, setLoading] = useState(true);
    
    /* Connect With Firebase Authentication */
    function signIn(email,password){
        return auth.signInWithEmailAndPassword(email,password);
    }
    function signOut(){
        return auth.signOut();
    }
    useEffect(()=>{
        const checkAuthentication = auth.onAuthStateChanged((user)=>{
            setCurrentUser(user);
            setLoading(false);
        });
        return checkAuthentication;
    },[])
    

    const value = {
        currentUser,
        signIn,
        signOut
    }
    /*
    Return COntext Provider Values to all its children 
     */
    return (
        <UserLogin.Provider value={value}>
            {!loading && children}
        </UserLogin.Provider>
    );
}